import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm } from "redux-form";
import { getParaSystem } from "../../../actions/utility_action";
import { setItem } from "../../../components/helpers/function";
import { ToastNotification } from "../../../components/helpers/notification";
import { getData2, postData2 } from "../../../config/axios";
import FormParameterPoint from "./FormParameterPoint";

const maptostate = (state) => {
  return {
    DataSystem: state.utility.getParaSystem,
  };
};
class SettingSystem extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);

    this.state = {
      activeTab: "1",
      kategori_harga : false,
      isLoading : false
    };
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
    }
  }
  componentDidMount() {
    window.history.pushState(null, document.title, window.location.href);
    window.addEventListener("popstate", function (event) {
      window.history.pushState(null, document.title, window.location.href);
    });
    this.props.dispatch(getParaSystem());

    this.koadKategori()
  }


  koadKategori(){
    getData2("para-system/key/kategori_harga").then((res) => {
      // console.log(res.data.value);
      if (res.data.value === "harga_100%") {
        this.setState({
          kategori_harga: true,
        });
      } else {
        this.setState({
          kategori_harga: false,
        });
      }


    });
  }

  handleSubmit(data) {
    let kategori_harga = {
      key: "KATEGORI_HARGA",
      value: data.kategori_harga,
      type: "kategori_harga",
    };
    let type_poin = {
      key: "TIPE_POIN",
      value: data.type_poin,
      type: "type_poin",
    };
    let cashback = {
      key: "CASHBACK_POIN",
      value: data.cashback_poin,
      type: "cashback_poin",
    };
    let module = {
      key: "SEMI_GROSIR_MODULE",
      value: data.module_semi_grosir.toString() || 'false',
      type: "module",
    };

    let hasil = [type_poin, kategori_harga,cashback,module];
    this.setState({
      isLoading : true
    })
    postData2("para-system", hasil)
      .then((res) => {
        ToastNotification("success", "Setting System Berhasil Diubah");
        this.props.dispatch(getParaSystem());
        this.koadKategori()
        getData2('para-system/type/module').then((res)=>{
          // console.log(res);
          setItem('module',(res.data.filter((list)=> list.value === 'true')))
          localStorage.removeItem('saldo_supplier')
        })
        this.setState({
          isLoading : false
        })

        setTimeout(() => {
          window.location.reload()
        }, 1000);
      })
      .catch((err) => {
        // console.log(err);
        this.setState({
          isLoading : false
        })
      });
      this.koadKategori()
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Dashboard</Link>
          </li>
          <li className="breadcrumb-item">Setting System</li>
        </ol>
        <h1 className="page-header">Setting System</h1>
       
            <FormParameterPoint
              isLoading={this.state.isLoading}
              DataSystem={this.props.DataSystem}
              kategoriharga={this.state.kategori_harga}
              onSubmit={(data) => this.handleSubmit(data)}
            />
         
        {/* End Tambah Blank Page  */}
      </div>
    );
  }
}

// export default SettingSystem;
SettingSystem = reduxForm({
  form: "SettingSystem",
  enableReinitialize: true,
})(SettingSystem);
export default connect(maptostate, null)(SettingSystem);
